
html{height:100%
}
body{font-family:"Microsoft YaHei",sans-serif;font-size:100%
}
h1{font-family:"Microsoft YaHei",sans-serif;font-size:2.25em;color:#DA7D68;text-align:center;font-weight:800
}
h2{font-family:"Microsoft YaHei",sans-serif;font-size:1.75em;color:#DA7D68;text-align:center;font-weight:800
}
h3{font-size:1.25em;font-family:"Microsoft YaHei",sans-serif;color:#95989A;font-weight:400
}
p{font-size:1em;color:#95989A;font-weight:400
}
pre{font-family:"Microsoft YaHei",sans-serif;font-size:1em;color:#95989A;font-weight:400;white-space:pre-wrap;word-wrap:break-word
}
img{width:100%
}
#app{padding:0 0.5em
}
@media screen and (min-width: 640px){
#app{width:640px;margin:0 auto
}
}
@media screen and (min-width: 768px){
body{font-size:150%
}
#app{width:768px;margin:0 auto
}
}



